/** Adapted from /contrib/auto-render/auto-render.js at github.com/Khan/KaTeX */

import KaTeX from "katex";
import { Delimiter } from "./types";
import splitAtDelimiters from "./splitAtDelimiters";
import { mhchemParser } from "mhchemparser";

export type Macros = { [name: string]: string };

export default function renderLatexInTextAsHTMLString(
  text: string,
  delimiters: Delimiter[],
  strict: boolean,
  macros?: Macros
): string {
  const data = splitAtDelimiters(text, delimiters);
  const fragments = [];

  for (const element of data) {
    const { type, data, display } = element;

    if (type === "text") {
      fragments.push(data);
    } else {
      const latex = data;
      const displayMode = display;

      try {
        const parsedLatex = mhchemParser.toTex(latex, "tex");

        const rendered = KaTeX.renderToString(parsedLatex, {
          displayMode,
          macros,
          trust: true,
        });
        fragments.push(rendered);
      } catch (error) {
        if (strict) {
          throw error;
        }
        fragments.push(data);
      }
    }
  }

  return fragments.join("");
}
