import logo from "./logo.svg";
import "./App.css";
import "katex/dist/katex.min.css";
import Latex from "./react-latex-next-master/src";
import { useState } from "react";

function App() {
  const [value, setValue] = useState("$\\ce{H2SO4}$ or $\\pu{1,2e3 kJ}$");
  const chemExample = "$\\ce{(NH4)2S}$";
  const physExample = "$\\pu{123 mm2}$";

  return (
    <div className="App">
      <div className="inner">
        <p>
          <ul>
            <li>
              Everything that's not regular text needs to be wrapped in{" "}
              <code>$</code>
            </li>
            <li>
              Chemical equations are written as <code>{chemExample}</code> and
              physical units as <code>{physExample}</code> - full documentation{" "}
              <a
                href="https://mhchem.github.io/MathJax-mhchem/"
                target="_blank"
              >
                here
              </a>
            </li>
          </ul>
        </p>
        <h3>Input text</h3>
        <textarea
          value={value}
          onChange={(event) => {
            setValue(event.target.value);
          }}
        ></textarea>
        <h3>Output text</h3>
        <Latex>{value}</Latex>
      </div>
    </div>
  );
}

export default App;
